<template>
  <div>
    <div class="tabs is-boxed" style="margin-bottom: 0">
      <ul>
        <li :class="currentTab === null && 'is-active'">
          <a @click="$emit('select', null)">Tasks</a>
        </li>
        <li
          v-for="id in tabs.list" :key="id"
          :class="currentTab === id && 'is-active'"
        >
          <a @click="$emit('select', id)">
            <span>
              {{getTaskDescription(id, tabs.data[id])}}
            </span>
            <span class="icon">
              <i
                class="iconfont icon-close"
                @click.stop="$emit('remove', id)"
                style="cursor: pointer"
              ></i>
            </span>
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TaskTabs',

  props: {
    currentTab: {
      required: true
    },

    tabs: {
      required: true
    }
  },

  methods: {
    getTaskDescription (id, task) {
      if (id === -1) return 'New Task'
      else if (task) {
        if (task.description === '') {
          return 'No Description'
        } else {
          return task.description
        }
      }
    }
  }
}
</script>

<style lang="sass" scoped>
.tabs
  &.is-boxed
    a
      height: 45px
      border-top: none
      border-radius: 0
    li
      &:first-child
        a
          border-left: none
      &.is-active
        a
          border-bottom-width: 1px
      a
        .icon
          margin-right: -0.5rem
          &:hover
            background: #dbdbdb
            border-radius: 2rem
</style>
