<template>
  <div class="tasks full-container is-flex is-flex-column">
    <TaskTabs
      :tabs="tabs"
      :currentTab="currentTab"
      :isTaskListMatched="isTaskListMatched"
      @remove="handleTabDelete"
      @select="currentTab = $event"
    />

    <template v-if="isInitialized">
      <TaskDetail
        v-for="id in tabs.list" :key="id"
        :id="id"
        :currentTab="currentTab"
        :item="tasks.data[currentTab]"
        :codes="codes"
        @add="handleTaskAdd"
        @save="handleTaskSave"
        @mounted="handleTabMounted"
      />
    </template>

    <div
      class="is-flex-auto is-flex-column"
      :class="currentTab === null ? 'is-flex' : 'is-hidden'"
    >
      <TaskTableTopbar
        :query="query"
        :selectStatus="selectStatus"
        @add="handleTabCreate"
        @search="handleSearch"
        @delete="$wait(handleTaskDelete)"
        @input-query="query = $event"
      />

      <Loader v-if="isLoading.length" />

      <div
        class="is-flex-auto"
        style="padding: 0.5rem; padding-top: 0"
        v-else-if="$router.hasMatched({ name: 'task list' })"
      >
        <TaskTable
          :tasks="tasks"
          :selectStatus="selectStatus"
          @select-all="handleSelectAll"
          @select-task="handleTaskSelect"
          @task-detail-click="handleTabCreate"
        />
      </div>

      <div style="margin: 0.5rem; margin-top: 0">
        <Pagination
          :page="tasks.page"
          :total="tasks.total"
          :perPage="tasks.perPage"
          @change-page="handleChangePage"
        />
      </div>
    </div>
  </div>
</template>

<script>
import axios from '@/packages/axios'
import { sleep } from '@/utils/async'

import Pagination from '@/components/Pagination'

import TaskTabs from './Tabs'
import TaskTable from './Table'
import TaskTableTopbar from './Topbar'

import TaskDetail from '../TaskDetail'

export default {
  name: 'TaskManage',

  components: {
    TaskTabs,
    TaskTable,
    TaskTableTopbar,
    TaskDetail,
    Pagination
  },

  data () {
    return {
      currentTab: null,

      tabs: {
        list: [],
        data: {}
      },

      page: 1,
      query: '',
      perPage: 50,

      tasks: {
        list: [],
        data: {},
        page: 1,
        total: 0,
        perPage: 50,
      },

      codes: {
        list: [],
        data: {}
      },

      isLoading: [],
      isInitialized: false
    }
  },

  computed: {
    params () {
      const res = {}
      const { page, perPage, query } = this

      res.page = page
      res.perPage = perPage
      if (query !== '') {
        res.query = query
      }

      return res
    },

    isTaskListMatched () {
      return this.$route.path === '/tasks'
    },

    selectStatus () {
      let hasSelectedItem = false
      let hasNotSelectedItem = false

      for (let key in this.tasks.data) {
        const code = this.tasks.data[key]

        if (code.isSelected === true) {
          hasSelectedItem = true
        } else {
          hasNotSelectedItem = true
        }

        if (hasSelectedItem && hasNotSelectedItem) {
          return 'minus'
        }
      }

      if (hasSelectedItem) return true
      else return false
    },

    selectedTasks () {
      const res = []
      this.tasks.list.forEach(id => {
        const task = this.tasks.data[id]
        task.isSelected && res.push(task)
      })

      return res
    }
  },

  methods: {
    // initializers

    async handleSleep () {
      this.isLoading.push(0)
      await sleep(666)
      this.isLoading.pop()
    },

    getTasks () {
      const handleResponse = response => {
        const data = {}
        const list = []
        const dataSource = response.data
        dataSource.data.forEach(item => {
          list.push(item.id)
          item.isSelected = false
          data[item.id] = item
        })

        this.tasks = {
          data, list,
          page: dataSource.page,
          total: dataSource.total,
          perPage: dataSource.perPage
        }
      }

      this.isLoading.push(0)

      axios.post('tasks/search', this.params)
        .then(handleResponse)
        .finally(() => this.isLoading.pop())
    },

    getCodes () {
      const handleResponse = response => {
        const data = {}
        const list = []
        response.data.forEach(item => {
          list.push(item.code)
          data[item.code] = item
        })

        this.codes = { data, list }
      }

      this.isLoading.push(0)
      axios.post('codes/search')
        .then(handleResponse)
        .finally(() => this.isLoading.pop())
    },

    // Tabs handlers

    handleTabCreate (id) {
      const { list } = this.tabs

      if (list.indexOf(id) === -1 && list.length <= 5) {
        list.push(id)
      }

      if (list.indexOf(id) !== -1) {
        this.currentTab = id
      }
    },

    handleTabDelete (id) {
      this.currentTab = null

      const { list, data } = this.tabs

      list.splice(list.indexOf(id), 1)
      this.$delete(data, id)
    },

    handleTabMounted (task) {
      const { data } = this.tabs

      this.$set(data, task.id, task)
    },

    // Topbar handlers

    handleSelectAll () {
      const tasks = this.tasks.list.map(id => this.tasks.data[id])

      switch (this.selectStatus) {
        case 'minus':
        case false:
          tasks.forEach(task => task.isSelected = true)
          break
        case true:
          tasks.forEach(task => task.isSelected = false)
      }
    },

    handleTaskDelete () {
      const ids = this.selectedTasks.map(task => task.id)

      if (ids.length === 0) return

      const callback = () => {
        ids.forEach(id => {
          this.tasks.list.splice(this.tasks.list.indexOf(id), 1)
          this.$delete(this.tasks.data, id)
        })
      }

      return axios.post('tasks/delete', { ids })
        .then(callback).catch(error => console.log(error))
    },

    handleTaskAdd (task) {
      const { list, data } = this.tabs
      const { id } = task

      this.currentTab = id
      this.$delete(data, -1)
      this.$set(list, list.indexOf(-1), id)
      this.$set(data, id, task)

      task.isSelected = false
      this.tasks.list.unshift(id)
      this.$set(this.tasks.data, id, task)
    },

    handleTaskSave (task) {
      task.isSelected = false
      this.$set(this.tasks.data, task.id, task)
    },

    handleSearch () {
      this.page = 1
      this.getTasks()
    },

    // Task Table Handlers

    handleTaskSelect (id) {
      const task = this.tasks.data[id]

      task.isSelected = !task.isSelected
    },

    handleChangePage (page) {
      this.page = page
      this.getTasks()
    }
  },

  async created () {
    this.isInitialized = false

    await this.handleSleep()
    await this.getCodes()
    await this.getTasks()

    this.isInitialized = true
  }
}
</script>
